@import "../../styles/helpers/_helpers";

.first {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;

    &__inner {
        flex: 1 1 auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
        position: relative;
        z-index: 0;
        @include max-screen(991px) {
            margin: 0 -10px;
        }

        &::after {
            position: absolute;
            content: '';
            background-color:#00bfff;
            width: 40%;
            top: 0;
            left: 0;
            bottom: 0;
            height: 100%;
            z-index: -1;
            @include max-screen(767px) {
                width: 25%;
            }
            @include max-screen(599px) {
                width: 40%;
            }
        }
    }
    &__info {
        display: flex;
        flex-flow: column;
        align-items: center;
        justify-content: center;
        position: relative;
        height: 100vh;
        width: 100%;
        text-transform: uppercase;

        &::before {
            position: absolute;
            content: '';
            top: 10%;
            left: 20%;
            width: 60%;
            height: 80%;
            transform: translate3d(0, 0, 0);
            background: transparent;
            border: 3px solid #101010;
            @include max-screen(767px) {
                left: 10%;
                width: 80%;
                height: 80%;
            }
            @include max-screen(600px) {
                border: 3px solid transparent;
            }
        }
    }
    &__title-block {
        margin-left: 345px;
        @include max-screen(767px) {
            margin-left: 200px;
        }
        @include max-screen(600px) {
            margin-left: 0;
            width: 300px;
            text-align: right;
        }
    }
    .title {
        font-family: 'Neuton';
        font-size: 100px;
        background-color: white;
        padding-bottom: 15px;
        line-height: 1.2;
        font-weight: 400;
        z-index: 1;
        @include max-screen(991px) {
            font-size: 80px;
        }
        @include max-screen(600px) {
            background-color: transparent;
            font-size: 60px;
        }
    }
    &__name {
        font-family: 'Open Sans Condensed', sans-serif;
        font-size: 50px;
        line-height: 1.2;
        font-weight: 400;
        color: #818181;
        @include max-screen(991px) {
            font-size: 30px;
        }
    }
}
