.first {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.first__inner {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  position: relative;
  z-index: 0;
}

.first__inner::after {
  position: absolute;
  content: '';
  background-color: #00bfff;
  width: 40%;
  top: 0;
  left: 0;
  bottom: 0;
  height: 100%;
  z-index: -1;
}

.first__info {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100vh;
  width: 100%;
  text-transform: uppercase;
}

.first__info::before {
  position: absolute;
  content: '';
  top: 10%;
  left: 20%;
  width: 60%;
  height: 80%;
  transform: translate3d(0, 0, 0);
  background: transparent;
  border: 3px solid #101010;
}

.first__title-block {
  margin-left: 345px;
}

.first .title {
  font-family: 'Neuton';
  font-size: 100px;
  background-color: white;
  padding-bottom: 15px;
  line-height: 1.2;
  font-weight: 400;
  z-index: 1;
}

.first__name {
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 50px;
  line-height: 1.2;
  font-weight: 400;
  color: #818181;
}

@media only screen and (max-width: 991px) {
  .first__inner {
    margin: 0 -10px;
  }

  .first .title {
    font-size: 80px;
  }

  .first__name {
    font-size: 30px;
  }
}

@media only screen and (max-width: 767px) {
  .first__inner::after {
    width: 25%;
  }

  .first__info::before {
    left: 10%;
    width: 80%;
    height: 80%;
  }

  .first__title-block {
    margin-left: 200px;
  }
}

@media only screen and (max-width: 600px) {
  .first__info::before {
    border: 3px solid transparent;
  }

  .first__title-block {
    margin-left: 0;
    width: 300px;
    text-align: right;
  }

  .first .title {
    background-color: transparent;
    font-size: 60px;
  }
}

@media only screen and (max-width: 599px) {
  .first__inner::after {
    width: 40%;
  }
}